.contact{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
margin: 200px 0 100px;
align-items: center;
}
.contact>h1{
    color: white !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.contact__container{
    border: 1px solid white;
    width: 600px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    margin-bottom: 20px;
   

}

.contact__container>p{
    font-size: 20px;
    text-align: center;
    padding-bottom: 30px;
    color: white;
}
.contact__container>p >span{
    color: #f64c08;
}
.contact__icons >a >button > span > .MuiSvgIcon-root{
    color: white;
    font-size: 30px;
 
}
.contact__icons >a >button{
    color: white;
    cursor: pointer;
    transition: transform 200ms ease-in-out;

}
.contact__icons >a >button:hover{
    transform: scale(1.3);
}