@import url("https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Pro:wght@200&family=Montserrat:wght@300;600&display=swap");

.skillContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 200px 0;
}

.skillContainer__text {
  color: white;
  flex: 0.6;
  text-align: left;
  margin-bottom: 20px;
  max-width: 500px;
}

.skillContainer__text > h2 {
  padding-top: 100px;
  font-size: 30px;
}

.skillContainer__image {
  flex: 0.4;
  text-align: center;
  margin-right: 100px;
}

.skillContainer__image > img {
  width: 600px;
  border-radius: 3px;
  object-fit: contain;
}

.skillContainer__skillSet > h5 {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  padding-bottom: 23px;
  padding-top: 30px;
}

.skillContainer__slider > .MuiLinearProgress-colorPrimary {
  background-color: rgb(88, 88, 88);
}
.skillContainer__slider1 > div > .MuiLinearProgress-barColorPrimary {
  background-color: tomato;
}
.skillContainer__slider2 > div > .MuiLinearProgress-barColorPrimary {
  background-color: rgb(228, 213, 0);
}
.skillContainer__slider3 > div > .MuiLinearProgress-barColorPrimary {
  background-color: rgb(64, 143, 233);
}
.skillContainer__slider4 > div > .MuiLinearProgress-barColorPrimary {
  background-color: rgb(228, 213, 0);
}
.skillContainer__slider5 > div > .MuiLinearProgress-barColorPrimary {
  background-color: rgb(78, 146, 0);
}
.skillContainer__slider6 > div > .MuiLinearProgress-barColorPrimary {
  background-color: rgba(126, 155, 248, 0.616);
}
