.project>img{
    width: 350px;
    height: 179px;
}

.project{
    width: 350px;
    height: 179px;
    margin: 15px;
    transition: transform 100ms ease-in;
}

.project:hover{
    color: white;
    background-color: #f64c08;
    transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}


.project__content{
    padding: 10px;
}
.project__content>h4{
    padding-bottom: 5px;
}
.project__content>p{
    font-size: 15px;
}